import React, {useEffect} from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import SwipeableViews from 'react-swipeable-views';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import AppBar from '@material-ui/core/AppBar';

import Typography from '@material-ui/core/Typography';
import Zoom from '@material-ui/core/Zoom';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import UpIcon from '@material-ui/icons/KeyboardArrowUp';
import { green, grey } from '@material-ui/core/colors';
import Box from '@material-ui/core/Box';
import MicIcon from '@material-ui/icons/Mic';

import {useAuth} from '../../App';

import ChatHome from './ChatHome';
import Room from './Room';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`action-tabpanel-${index}`}
      aria-labelledby={`action-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `action-tab-${index}`,
    'aria-controls': `action-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    position: 'relative',
    minHeight: '80vh',
    backgroundColor: 'white',
  },
  fab: {
    position: 'absolute',
    bottom: theme.spacing(13),
    right: theme.spacing(2),
  },
  fabGreen: {
    color: theme.palette.common.white,
    backgroundColor: green[500],
    '&:hover': {
      backgroundColor: green[600],
    },
  },
}));

export default function ChatSpace({addCallBack, deleteCallBack, webSocket}) {
  const classes = useStyles();
  const theme = useTheme();
  
  const auth = useAuth()

  const [value, setValue] = React.useState(0);
  const [onlinePersonalsList, setOnlinePersonalsList] = React.useState([]);
  const [currentRoom, setCurrentRoom] = React.useState();

  useEffect(() => {
    console.log(addCallBack)
    addCallBack({
      id: 'ChatSpace',
      type: [
        'chat',
        'online_personals_list',
        'simple_messages'
      ],

      run: (data) => {
        console.log('je suis la depuis chaspace')

      console.log(data)
      if (data.type === 'online_personals_list'){
        setOnlinePersonalsList(data.content)
      }
      else if (data.type === 'simple_messages'){
        setCurrentRoom(data.content)
        setValue(1);
      }
      else if(data.type === "chat"){
        if (data.content.type === "message"){
          console.log('message set')
          let room = {...currentRoom};
              room.messages = [...room.messages, data.content.message]
              console.log(room)
            setCurrentRoom(room)
        }
      }
      else{

      }

      }
    })
    return () => {
      deleteCallBack('ChatSpace')
    }
  }, [])

  useEffect(() => {
    webSocket.send(JSON.stringify({
      type: 'get_online_personals_list',
      })
    )
    
  }, [currentRoom])

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const getRoom = (id) => {
    webSocket.send(JSON.stringify({
      type: 'get_simple_messages',
      content: {
        room: id
      }
      })
    )
  }

  const handleChangeIndex = (index) => {
    setValue(index);
  };

  const sendChatMessage = (message, to) =>{
    const code = new Date()

    console.log('sendChatMessage')
    webSocket.send(JSON.stringify({
      type: 'chat',
      content: {
        room_type: 'simple',
        author: auth.user.id,
        to,
        message_type: message.type,
        message: message.content,
        message_code: code.getTime()
      }
      })
    )
  }

  const transitionDuration = {
    enter: theme.transitions.duration.enteringScreen,
    exit: theme.transitions.duration.leavingScreen,
  };

  const fabs = [
    {
      color: 'primary',
      className: classes.fab,
      icon: <AddIcon />,
      label: 'Add',
    },
    {
      color: 'secondary',
      className: classes.fab,
      icon: <MicIcon />,
      label: 'Vocal',
    },
    {
      color: 'inherit',
      className: clsx(classes.fab, classes.fabGreen),
      icon: <UpIcon />,
      label: 'Expand',
    },
  ];

  return (
    <Paper className={classes.root}>
{/*       <AppBar position="static" color="default">
        <Tabs
          value={value}
          onChange={handleChange}
          indicatorColor="primary"
          textColor="primary"
          variant="fullWidth"
          aria-label="action tabs example"
        >
          <Tab label="messages" {...a11yProps(0)} />
          <Tab label="room" {...a11yProps(1)} />
          <Tab label="video" {...a11yProps(2)} />
        </Tabs>
      </AppBar> */}
      <SwipeableViews
        axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
        index={value}
        onChangeIndex={handleChangeIndex}
      >
        <TabPanel value={value} index={0} dir={theme.direction}>
          <ChatHome getRoom={getRoom} onlinePersonalsList={onlinePersonalsList}/>
        </TabPanel>
        <TabPanel value={value} index={1} dir={theme.direction}>
          <Room changeView={setValue} currentRoom={currentRoom} sendChatMessage={sendChatMessage}/>
        </TabPanel>
        <TabPanel value={value} index={2} dir={theme.direction}>
          Video
        </TabPanel>
      </SwipeableViews>
      {fabs.map((fab, index) => (
        <Zoom
          key={fab.color}
          in={value === index}
          timeout={transitionDuration}
          style={{
            transitionDelay: `${value === index ? transitionDuration.exit : 0}ms`,
          }}
          unmountOnExit
        >
          <Fab aria-label={fab.label} className={fab.className} color={fab.color}>
            {fab.icon}
          </Fab>
        </Zoom>
      ))}
    </Paper>
  );
}