import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import AvatarIcon from '@material-ui/icons/PersonOutline';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-force-tabpanel-${index}`}
      aria-labelledby={`scrollable-force-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `scrollable-force-tab-${index}`,
    'aria-controls': `scrollable-force-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.paper,
    minHeight: '85vh',
    width: "100%",
    flexGrow: 1,
    position: "relative",
    
  },
}));

export default function ChatHome({onlinePersonalsList, getRoom}) {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    getRoom(newValue)
    setValue(newValue)
  };

  return (
    <div className={classes.root}>
      <AppBar position="absolute" color="primary">
        <Tabs
          value={value}
          onChange={handleChange}
          variant="scrollable"
          scrollButtons="on"
          aria-label="scrollable force tabs example"
        >
            {
                onlinePersonalsList.map((personal, key)=>{
                    return <Tab key={key} value={personal.id} label={personal.username} icon={<AvatarIcon />} {...a11yProps(personal.id)} />
                })
            }
        </Tabs>
      </AppBar>
      Liste de derni√®re conversation
    </div>
  );
}